<template>
  <div id="userRegisterView">
    <h2 style="margin-bottom: 16px"></h2>
    <a-form
      style="max-width: 480px; margin: 0 auto"
      label-align="left"
      auto-label-width
      :model="form"
      @submit="handleSubmit"
    >
      <a-form-item field="userAccount" label="">
        <a-input v-model="form.userAccount" placeholder="" />
      </a-form-item>
      <a-form-item field="userPassword" tooltip=" 8 " label="">
        <a-input-password
          v-model="form.userPassword"
          placeholder=""
        />
      </a-form-item>
      <a-form-item field="checkPassword" tooltip=" 8 " label="">
        <a-input-password
          v-model="form.checkPassword"
          placeholder=""
        />
      </a-form-item>
      <a-form-item>
        <a-button type="primary" html-type="submit" style="width: 120px">
          
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script setup lang="ts">
import { reactive, ref } from "vue";
import { UserControllerService, UserLoginRequest } from "../../../generated";
import message from "@arco-design/web-vue/es/message";
import { useRouter } from "vue-router";
import { useStore } from "vuex";

/**
 * 
 */
const form = reactive({
  userAccount: "",
  userPassword: "",
  checkPassword: "",
} as UserLoginRequest);

// const userPasswordAgain = ref("");

const router = useRouter();
const store = useStore();

/**
 * 
 * @param data
 */
const handleSubmit = async () => {
  const res = await UserControllerService.userRegisterUsingPost(form);
  // 
  if (res.code === 0) {
    message.success("");
    router.push({
      path: "/user/login",
      replace: true,
    });
  } else {
    message.error("" + res.message);
  }
};
</script>
