<template>
  <div id="manageQuestionView">
    <a-table
      :ref="tableRef"
      :columns="columns"
      :data="dataList"
      :pagination="{
        showTotal: true,
        pageSize: searchParams.pageSize,
        current: searchParams.current,
        total,
      }"
      @page-change="onPageChange"
    >
      <template #optional="{ record }">
        <a-space>
          <a-button type="primary" @click="doUpdate(record)"> </a-button>
          <a-button status="danger" @click="doDelete(record)"></a-button>
        </a-space>
      </template>
    </a-table>
  </div>
</template>

<script setup lang="ts">
import { onMounted, ref, watchEffect } from "vue";
import { Question, QuestionControllerService } from "../../../generated";
import message from "@arco-design/web-vue/es/message";
import { useRouter } from "vue-router";

const tableRef = ref();

const dataList = ref([]);
const total = ref(0);
const searchParams = ref({
  pageSize: 10,
  current: 1,
});

const loadData = async () => {
  const res = await QuestionControllerService.listQuestionByPageUsingPost(
    searchParams.value
  );
  if (res.code === 0) {
    dataList.value = res.data.records;
    total.value = res.data.total;
  } else {
    message.error("" + res.message);
  }
};

/**
 *  searchParams 
 */
watchEffect(() => {
  loadData();
});

/**
 * 
 */
onMounted(() => {
  loadData();
});

// {id: "1", title: "A+ D", content: "", tags: "[""]", answer: "", submitNum: 0,}

const columns = [
  {
    title: "id",
    dataIndex: "id",
  },
  {
    title: "",
    dataIndex: "title",
  },
  {
    title: "",
    dataIndex: "content",
  },
  {
    title: "",
    dataIndex: "tags",
  },
  {
    title: "",
    dataIndex: "answer",
  },
  {
    title: "",
    dataIndex: "submitNum",
  },
  {
    title: "",
    dataIndex: "acceptedNum",
  },
  {
    title: "",
    dataIndex: "judgeConfig",
  },
  {
    title: "",
    dataIndex: "judgeCase",
  },
  {
    title: "id",
    dataIndex: "userId",
  },
  {
    title: "",
    dataIndex: "createTime",
  },
  {
    title: "",
    slotName: "optional",
  },
];

const onPageChange = (page: number) => {
  searchParams.value = {
    ...searchParams.value,
    current: page,
  };
};

const doDelete = async (question: Question) => {
  const res = await QuestionControllerService.deleteQuestionUsingPost({
    id: question.id,
  });
  if (res.code === 0) {
    message.success("");
    loadData();
  } else {
    message.error("");
  }
};

const router = useRouter();

const doUpdate = (question: Question) => {
  router.push({
    path: "/update/question",
    query: {
      id: question.id,
    },
  });
};
</script>

<style scoped>
#manageQuestionView {
}
</style>
